@keyframes scaleIn {
    0% {
        opacity: 0;
        transform: scale(0.9);
    }
    50% {
        opacity: 0.3;
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
}

.App {
    display: flex;
    .section1 {
        flex: 50%;
    }
    .section2 {
        flex: 50%;
    }
}

.container {
    // border: dashed green 2px;
    margin: 50px auto;
    padding: 20px 20px;
    border-radius: 5px;
    background: rgb(255, 255, 255);
    background: linear-gradient(90deg, rgba(255, 255, 255, 1) 0%, rgba(4, 3, 60, 1) 66%, rgba(203, 230, 220, 1) 77%, rgba(62, 62, 166, 1) 90%);
    .error {
        color: red;
        background-color: lighten($color: #292a2e, $amount: 8%) !important;
        border-radius: 5px;
        padding-left: 3px;
        padding-bottom: 4px;
        width: auto;
        align-items: baseline;
    }
}

//=====================section1_inputs section1__parameters=====================
.section1__inputs {
    display: flex;
    flex-direction: column;
     :first-child {
        display: flex;
        flex-direction: row;
    }
    &__cutDownInput {
        width: 70%;
        border-radius: 5px;
        padding: 10px 20px;
    }
    &__submit {
        width: 30%;
        padding: 10px 20px;
        border-radius: 5px;
        text-align: center;
    }
}

//====================section2__authorization========================
.section2__authorization {
    display: flex;
    flex-direction: column;
    max-width: 300px;
    margin: 0 auto;
    &__inputs {
        display: flex;
        flex-direction: column;
        margin-bottom: 15px;
         :first-child {
            margin-bottom: 5px;
        }
    }
    &__buttons {
        display: flex;
        flex-direction: column;
         :nth-child(2) {
            margin-bottom: 5px;
        }
         :nth-child(1) {
            margin-bottom: 5px;
        }
    }
}

.section1__parameters {
    display: flex;
    flex-direction: column;
    div {
        max-width: 177px;
    }
    &__select {
        // margin-left: 10px;
        float: right;
        height: 21px !important;
    }
    .css-b62m3t-container {
        display: inline-block !important;
    }
}

.customSelect {
    // &-container {
    //     // max-width: 200px !important;
    // }
    &__control {
        background-color: transparent !important;
        border: none !important;
        border-radius: 0 !important;
        border-bottom: 1px solid #66676e !important;
        box-shadow: none !important;
        color: rgb(0, 14, 145) !important;
        min-height: auto !important;
        // max-width: 21px !important;
        // height: 21px !important;
    }
    &__single-value {
        border-radius: 0.5rem !important;
    }
    &--menu-is-open {
        border-color: #e42c64 !important;
    }
    &__value-container {
        padding: 0 !important;
    }
    &__input-container {
        color: #e42c64 !important;
    }
    &__indicator-separator {
        display: none !important;
    }
    &__indicator {
        &:hover {
            color: #e42c64 !important;
        }
    }
    &__single-value {
        color: #ffffff !important;
    }
    &__placeholder {
        color: #ffffff !important;
    }
    &__menu {
        // max-width: 200px;
        background-color: #292a2e !important;
        color: white;
        animation: scaleIn 0.35s ease-in-out;
        width: auto !important;
    }
    &__option {
        cursor: pointer !important;
        &--is-focused {
            background-color: lighten($color: #292a2e, $amount: 8%) !important;
        }
    }
    &__indicator {
        padding: 0 !important;
    }
    // &__select {
    //     height: 21px !important;
    // }
}

